import React, { useState } from "react";
import { timeConverter, timeNow } from "../../assets/function/timeConverter";
import ReduxWeatherForm from "./weatherForm/weatherForm";
import s from "./weather.module.css";
import arrowUp from "../../assets/images/arrowUp.svg";
import arrowDown from "../../assets/images/arrowDown.svg";
import { windDirection } from "../../assets/function/windDirection";

const Weather = (props) => {
  const [activePage, setActivePage] = useState(false);

  const onSubmitForm = (formData) => {
    props.getWeather(formData.city);
  };
  return (
    <div className={s.weatherWrapper}>
      <div className={s.weather}>
        <div
          className={
            activePage
              ? `${s.weatherFront} ${s.weatherFrontActive}`
              : `${s.weatherFront}`
          }
        >
          <ReduxWeatherForm
            onSubmit={onSubmitForm}
            errorActive={props.errorActive}
          />
          <div className={s.date}>{timeNow()}</div>
          <div>
            <div className={s.cityName}>{props.weather.name}</div>
            <div className={s.temperature}>
              {Math.round(props.weather.temp)}°c
            </div>
            <div className={s.temperatureDifference}>
              <div className={s.temperatureMin}>
                <img src={arrowDown} />
                {Math.round(props.weather.temp_min)}°c
              </div>
              <div className={s.temperatureMax}>
                <img src={arrowUp} />
                {Math.round(props.weather.temp_max)}°c
              </div>
            </div>
            <div className={s.weatherDescription}>
              {props.weather.weatherDescription}
            </div>
            <div
              className={s.weatherButton}
              onClick={() => {
                setActivePage(true);
              }}
            >
              More
            </div>
          </div>
        </div>
        <div
          className={
            activePage
              ? `${s.weatherBack} ${s.weatherBackActive}`
              : `${s.weatherBack} ${s.weatherBackNoActive}`
          }
        >
          <div
            className={s.wetherBackItem}
          >{`Humidity : ${props.weather.humidity}%`}</div>
          <div className={s.wetherBackItem}>
            Sunrise : {timeConverter(props.weather.sunrise)}
          </div>
          <div className={s.wetherBackItem}>
            Sunset : {timeConverter(props.weather.sunset)}
          </div>
          <div className={s.wetherBackItem}>
            Wind deg : {windDirection(props.weather.wind_deg)}
          </div>
          <div className={s.wetherBackItem}>
            Wind speed : {props.weather.wind_speed} m/h
          </div>
          <div
            className={s.weatherButton}
            onClick={() => {
              setActivePage(false);
            }}
          >
            Back
          </div>
        </div>
      </div>
    </div>
  );
};

export default Weather;
