import * as axios from "axios";

const apiKey = "8497e475027cc3d09dd08b9d2749be83";
export const weatherApi = {
  getWeatherData(city) {
    return axios
      .get(
        `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${apiKey}&units=metric`
      )
      .then((response) => {
        console.log(response);
        return response;
      })
      .catch((error) => {
        console.log("error");
        return error;
      });
  },
};
