import { stopSubmit } from "redux-form";
import { weatherApi } from "../api/api";

const SET_WEATHER = "SET-WEATHER";
const SET_ERROR = "SET-ERROR";

const initialState = {
  weather: {
    cityName: undefined,
    temp: undefined,
    temp_min: undefined,
    temp_max: undefined,
    pressure: undefined,
    humidity: undefined,
    sunrise: undefined,
    sunset: undefined,
    wind_deg: undefined,
    wind_speed: undefined,
    weatherDescription: undefined,
  },
  errorActive: false,
};

export const setWeather = (weather) => ({
  type: SET_WEATHER,
  temp: weather.main.temp,
  temp_min: weather.main.temp_min,
  temp_max: weather.main.temp_max,
  pressure: weather.main.pressure,
  humidity: weather.main.humidity,
  sunrise: weather.sys.sunrise,
  sunset: weather.sys.sunset,
  wind_deg: weather.wind.deg,
  wind_speed: weather.wind.speed,
  name: weather.name,
  weatherDescription: weather.weather[0].description,
});

export const setError = (errorActive) => ({
  type: SET_ERROR,
  errorActive,
});

const weatherReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_WEATHER: {
      return {
        ...state,
        weather: {
          name: action.name,
          temp: action.temp,
          temp_min: action.temp_min,
          temp_max: action.temp_max,
          pressure: action.pressure,
          humidity: action.humidity,
          sunrise: action.sunrise,
          sunset: action.sunset,
          wind_deg: action.wind_deg,
          wind_speed: action.wind_speed,
          weatherDescription: action.weatherDescription,
        },
      };
    }
    case SET_ERROR: {
      return {
        ...state,
        errorActive: action.errorActive,
      };
    }
    default:
      return state;
  }
};

export const getWeather = (city) => async (dispatch) => {
  const response = await weatherApi.getWeatherData(city);
  console.log(response);
  if (response.status === 200) {
    dispatch(setWeather(response.data));
    dispatch(setError(false));
  } else {
    dispatch(setError(true));
    console.log("eewrf");
    const error = "city not found";
    dispatch(stopSubmit("weatherForm", { _error: error }));
    return Promise.reject(error);
  }
};

export default weatherReducer;
