export const windDirection = (degN) => {
  const deg = Number(degN);
  switch (true) {
    case 350 <= deg || deg < 20: {
      return "N";
    }
    case 20 <= deg && deg < 40: {
      return "N/NE";
    }
    case 40 <= deg && deg < 60: {
      return "NE";
    }
    case 60 <= deg && deg < 80: {
      return "E/NE";
    }
    case 80 <= deg && deg < 110: {
      return "E";
    }
    case 110 <= deg && deg < 130: {
      return "E/SE";
    }
    case 130 <= deg && deg < 150: {
      return "SE";
    }
    case 150 <= deg && deg < 170: {
      return "S/SE";
    }
    case 170 <= deg && deg < 200: {
      return "S";
    }
    case 200 <= deg && deg < 220: {
      return "S/SW";
    }
    case 220 <= deg && deg < 240: {
      return "SW";
    }
    case 240 <= deg && deg < 260: {
      return "W/SW";
    }
    case 260 <= deg && deg < 290: {
      return "W";
    }
    case 290 <= deg && deg < 310: {
      return "W/NW";
    }
    case 310 <= deg && deg < 330: {
      return "NW";
    }
    case 330 <= deg && deg < 350: {
      return "N/NW";
    }
    default:
      return deg;
  }
};
